.card {
  background-color: #e9e8e8;
  border-radius: 10px;
  border: none;
  overflow-x: hidden;
  margin: 0px 0px 30px 0px;
}
.card img {
  scale: 1.2;
  width: 100%;
  max-height: 110px;
  object-fit: cover;
}

.card-title {
  margin-top: 5px;
  font-weight: 500;
  font-size: 18px;
}

.card-text {
  font-weight: 400;
  text-align: left;
  line-height: 20px;
  font-size: 12px;
}
